* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

nav {
  background: #f6faf8;
}

.nav__list {
  height: 10vh;
  display: flex;
  width: 50%;
  justify-content: space-around;
  align-items: center;
  margin-left: auto;
  list-style: none;
}

.nav__links {
  font-family: 'Major Mono Display', monospace;
  text-decoration: none;
  color: black;
  font-size: 18px;
}

.home, .about, .contact {
  height: 90vh;
  position: absolute;
  width: 100%;
}

.home__content, .about__content, .contact__content {
  height: 100%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  img {
    height: 70%;
  }
  h1 {
    font-family: 'Major Mono Display', monospace;
    font-size: 50px;
    margin: 30px 0px;
  }
  p {
    font-family: 'Literata', serif;
    font-size: 20px;
  }
}

.home {
  background-image: linear-gradient(to top, #48c6ef 0%, #6f86d6 100%);
}

.about {
  background-image: linear-gradient(to top, #feada6 0%, #f5efef 100%);
}

.contact {
  background-image: linear-gradient(45deg, #93a5cf 0%, #e4efe9 100%);
}
